<template>
  <v-app>
    <bm-right-sidebar></bm-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link">{{ item.text }}</v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto">Data</v-tab>
          <v-tab :key="1">Debug Messages</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="pointHeaders"
              :items="device.points"
              class="elevation-1 text-start"
              item-key="point_uuid"
              show-select
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>POINTS</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <!-- Refresh btn-->
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text">Refresh<v-icon x-small right dark>mdi-refresh</v-icon></v-btn >&nbsp;
                    <!-- Create btn-->
                    <v-btn @click="dialog = true" x-small color="success" class="white--text">Create<v-icon x-small right dark>mdi-plus</v-icon></v-btn>&nbsp;
                    <!-- Delete btn -->
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text">Delete<v-icon x-small right dark>mdi-delete</v-icon></v-btn>&nbsp;
                    <!-- Download btn -->
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="#009688" class="white--text" dark v-bind="attrs" v-on="on">Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <!-- Import btn -->
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on">Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click=" dialogImportExcel = true;" >Excel</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <!-- Export btn -->
                    <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" >Export<v-icon x-small right dark>mdi-export</v-icon></v-btn>&nbsp;
                    <v-btn @click="openDiscoverPointsDialog" x-small color="red" class="white--text">Discover Points</v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:item.point_enable="{ item }">
                <v-checkbox v-model="item.point_enable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)" color="primary">mdi-pencil</v-icon>
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>
              </template>

              <!-- header -->
              <template v-slot:header.point_enable="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('point_enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:no-data>No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="2">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">{{formTitle}}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.device_name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.point_name" label="Point name" required :rules="[() =>!isExistingvalue(editedItem.point_name, 'point_name') || 'Name Existed', rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.point_enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.point_object_id" label="point object id" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select v-model="editedItem.point_object_type" :items="point_object_types" label="point object type" required :rules="[rules.required]"></v-select>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 >1" step="1">Import</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 >2" step="2">Edit</v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport">Continue</v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="pointHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="point_uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport">mdi-delete</v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.point_uuid, 'point_uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.point_uuid, 'point_uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createPoint(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.point_uuid, 'point_uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.point_name="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.point_uuid, 'point_uuid')" v-model="item.point_name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.point_name" single-line style="width: 100px" required :rules="[() =>!isExistingvalue(item.point_name, 'point_name') || 'Name Existed', rules.required]"></v-text-field>
                </template>
                <template v-slot:item.point_enable="{ item }">
                  <v-checkbox v-model="item.point_enable"></v-checkbox>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1">Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid">Save</v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string' && editField != 'point_object_type'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>

    <!-- discover points dialog-->
    <v-dialog v-model="discoverPointsDialog" width="800px">
      <v-card >
        <v-card-title>
          <span class="headline">Discover Points</span>
        </v-card-title>
        <v-card-text class="text-start">
          <v-container>
            <v-row style="padding: 0 24px;">
              <v-col cols="12" sm="6" md="3">
                <v-checkbox v-model="user_options.add_points" label="add points"></v-checkbox>
              </v-col>
            </v-row>
            <v-expansion-panels :value="panel" multiple>
              <v-expansion-panel v-for="(key, index) in Object.keys(discoverPointsResult)" :key="index" >
                <v-expansion-panel-header>
                  <h3>{{toUpperTextKey(key)}}</h3>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <v-data-table
                    :headers="discoverPointsHeaders"
                    :items="discoverPointsResult[key]"
                    :hide-default-footer="true"
                    class="elevation-1"
                  ></v-data-table>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="discoverPointsDialog = false">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveDiscoverPoints">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-app>
</template>

<script>
import { isJsonString, copyToClipboard, exportJSON, exportCSV, exportExcel, toUpperTextKey, convertPointsValueToString } from '../../utils/utils';
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import BmRightSidebar from './BmRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';

export default {
  components: {
    MqttConsole,
    BmRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog
  },
  data: () =>({
    topic: 'rubix/points/debug',
    tab: 0,
    editedIndex: -1,
    action: '',
    device: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    pointHeaders: [
      { text: 'Actions', value: 'actions', width: '80px', sortable: false },
      { text: 'Name', value: 'point_name', width: '90px' },
      { text: 'Enable', value: 'point_enable', sortable: false },
      { text: 'Point Object Id', value: 'point_object_id', sortable: false },
      { text: 'Point Object Type', value: 'point_object_type', sortable: false },
      { text: 'UUID', value: 'point_uuid', sortable: false },
    ],
    point_object_types: ["analogInput", "analogOutput", "analogValue", "binaryInput", "binaryOutput", "multiStateInput", "multiStateOutput", "multiStateValue"],
    currentItem: {},
    editedItem: {
      point_name: 'Point',
      point_object_id: 1,
      point_enable: true,
      point_object_type: 'analogOutput',
    },
    defaultItem: {
      point_name: 'Point',
      point_object_id: 1,
      point_enable: true,
      point_object_type: 'analogOutput',
    },
    rules: {
      required: value =>!!value || 'Required.',
    },
    dataImport: '',
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    search: '',
    searchWizard: '',
    dialogMassEdit: false,
    editAll: {
      point_enable: false,
    },
    editField: "",
    intervalId: null,
    isClickSaveImport: false,
    dirs: [],
    tableProps: {...DATA_TABLE_PROPS},
    discoverPointsDialog: false,
    discoverPointsResult: {},
    user_options:{},
    panel: [],
    discoverPointsHeaders: [
      { text: 'Name', value: 'point_name'},
      { text: 'Point Object Id', value: 'point_object_id', sortable: false },
      { text: 'Point Value', value: 'point_value', sortable: false },
      { text: 'Point Object Type', value: 'point_object_type', sortable: false },
    ]
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() =>{
      next();
    });
  },
  mounted() {
    this.getData();
    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.device = await axios.get(`bacnet_master/device/${this.$route.params.id}`);
      this.getDirs();
      this.editedItem.device_uuid = this.defaultItem.device_uuid = this.device.device_uuid;
    },
    getDirs(){
      axios.get(`bacnet_master/network/${this.device.network_uuid}`).then((data)  =>{
        this.dirs= [{text: 'NETWORKS', link: '/bacnet-master/networks'}];
        this.dirs.push({text: `NET: ${data.network_name}`, link: `/bacnet-master/networks/${data.network_uuid}/devices`});
        this.dirs.push({text: `DEV: ${this.device.device_name}`});
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex >-1) {
          this.updatePoint(this.editedItem);
        } else {
          this.createPoint(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.currentItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/point/${this.currentItem.point_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() =>{
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() =>{
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport= false;
      this.$nextTick(() =>{
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length >0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try{
            await this.createMutiPoints(this.importArray);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){
          }
        }
      }
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length >0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event =>{
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet =>{
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i =>{
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.device_uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point=>{
        delete point.point_uuid;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName, item) {
      let points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      return points.find(p =>p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('points', data);
      }
      if (type === 'excel') {
        return exportExcel('points', data);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(data));
      }
    },
    updatePoint(data) {
      const _data = {
        point_name:data.point_name,
        point_enable:data.point_enable,
        point_object_id:data.point_object_id,
        point_object_type:data.point_object_type,
        device_uuid:data.device_uuid
      }
      axios.patch(`bacnet_master/point/${data.point_uuid}`, _data)
        .then(response =>{
          const index = this.device.points.findIndex(n =>n.point_uuid == response.point_uuid);
          Object.assign(this.device.points[index], response);
          this.$toasted.show('update success');
          this.close();
        });
    },
    async createPoint(data) {
      await axios.put(`bacnet_master/point`, data)
        .then(response =>{
          this.device.points.push(response);
          if (this.importArray && this.importArray.length >0) {
            const index = this.importArray.findIndex(n =>n.name == response.name);
            if(index >-1){
              Object.assign(this.importArray[index], response);
            }
          }
          this.$toasted.show('create success');
          this.close();
        });
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        point_enable: false,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({item}) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    async createMutiPoints(points) {
      const promises=[];
      for(const item of points) {
        item.device_uuid = this.device.device_uuid; //update to the current device_uuid
        if(!item.point_uuid) {
          promises.push(this.createPoint(item));
        }
      }
      await Promise.all(promises);
    },
    async saveDiscoverPoints(){
      const result = await axios.post(`bacnet_master/b/points/discover_points/${this.device.device_uuid}`, this.user_options);
      if(!result) return this.$toasted.show('fail!', { type: 'error' });
      Object.keys(result).forEach(key=>{
        if(typeof result[key] === 'object' && result[key] !== null){
          if(result[key].points){
            let points = [];
            Object.keys(result[key].points).forEach(type=>{
              const pointsByType = result[key].points[type];
              pointsByType.map(point=>{
                point.point_object_type = type
                return point;
              })
              points = points.concat(pointsByType);
            });
            this.discoverPointsResult = {...this.discoverPointsResult, [key]: points};
          }
        }
      });
      this.panel = Object.keys(this.discoverPointsResult).map((k, i) =>i);
      this.$toasted.show('success!', { type: 'success' });
    },
    openDiscoverPointsDialog(){
      this.discoverPointsDialog = true;
      this.discoverPointsResult = {};
      this.user_options= { add_points:true }
    },

  },
};
</script>
